

import Vue from 'vue'
import moment from 'moment';
import fetch from  '@/api/fetch';
import util from '@/common/js/util';
//
Vue.prototype.getUserInfo=function(){
    let value=util.getCookie('userInfo');
    if(value==''){
          return {
                user: {
                      realName: "",   //
                      gender: "", //
                      userName: "", //
                      emailAddress: "", //
                      manageOrgId: "", //
                      manageOrgName: "", //
                      companyId:"", //
                      companyName:"",  //
                      roleId: "", //
                      roleName: "", // 
                      realOrgId:"" ,// 
                      lastLoginTime: "", //
                      id:"" //
                  },
                  userDataRanges:[],
                  userRoles:[]
          }
    }
    return JSON.parse(value);
}


